import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import styled from "styled-components"
import { MdArrowDownward, MdPermIdentity, MdMailOutline } from "react-icons/md"

import Img from "gatsby-image"

const _Img = styled(Img)`
  color: black;
  z-index: -1;
  background-color: rgba(255, 255, 255, 0.8);
  height: 100%;
  width: auto;
  grid-column: 1 / span 6;
  grid-row: 1 / span 7;
  @media (min-width: 760px) {
    grid-column: 1 / span 10;
  }
`

const _Header = styled.header`
  color: white;
  height: 15%;
  height: 100vh;
  width: auto;
  display: grid;
  grid-gap: 0.5rem;
  grid-template-columns: min-content repeat(5, 1fr);
  grid-template-rows: 3rem repeat(4, 1fr) 1fr 1fr;
  align-items: start;
  @media (min-width: 760px) {
    grid-template-columns: 1fr min-content repeat(6, 1fr) minMax(200px, 350px) 1fr;
    grid-template-rows: repeat(5, 1fr) 1fr 1fr;
  }
  /* #### Mobile Phones Landscape #### */
  @media screen and (max-device-width: 640px) and (orientation: landscape) {
    height: 15%; /* sadly, it's not safe to use vh on mobile :-/ */
  }
`

const _Title = styled.h1`
  margin-left: 0.8rem;
  grid-column: 1 / span 4;
  grid-row: 2;
  @media (min-width: 760px) {
    margin-left: 0;
    grid-column: 2 / span 4;
  }
`

const _Occupation = styled.h3`
  margin-left: 0.8rem;
  grid-column: 1 / span 3;
  grid-row: 3;
  @media (min-width: 760px) {
    margin-left: 0;
    grid-column: 2 / span 3;
  }
`

const _ContactInfo = styled.div`
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-items: center;
  padding: 1rem;
  color: #3d3c3c;
  background-color: rgba(255, 255, 255, 0.7);

  max-width: 14rem;

  p {
    margin: 0;
  }

  grid-column: 1 / span 4;
  grid-row: 6;
  @media (min-width: 760px) {
    grid-column: 2;
  }
`

const _Summary = styled.div`
  color: #3d3c3c;
  padding: 1rem;
  p {
    margin: 0;
  }
  text-align: justify;
  background-color: rgba(255, 255, 255, 0.7);

  grid-column: 3 / span 4;
  grid-row: 4;
  @media (min-width: 760px) {
    grid-column: 9 / span 1;
    grid-row: 3;
  }
  @media screen and (min-device-width: 768px) and (max-device-width: 1024px) {
    grid-row: 5;
  }
`

const _AboutMe = styled.a`
  margin: 0;

  color: white;
  text-decoration: none;

  h2 {
    margin: 0;
    margin-right: 0.5rem;
  }

  grid-column: 1 / span 6;
  grid-row: 7;
  align-self: end;
  justify-self: center;

  display: flex;
  align-items: center;

  @media (min-width: 760px) {
    justify-self: start;
    grid-column: 7 / span 3;
    grid-row: 6;
  }
`

const _DownArrowIcon = styled(MdArrowDownward)`
  font-size: 2rem;
`

const _fontAndIcon = styled.p`
  display: flex;
  align-items: center;
  span {
    margin-left: 0.5rem;
  }
`

const LandingPage = () => {
  const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
        }
      }
      file(relativePath: { eq: "home.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 2000) {
            ...GatsbyImageSharpFluid_tracedSVG
          }
        }
      }
    }
  `)
  return (
    <_Header id="home">
      <_Img fluid={data.file.childImageSharp.fluid} />
      <_Title>{data.site.siteMetadata.title}</_Title>
      <_ContactInfo>
        <p>
          <strong>Contact Info</strong>
        </p>
        <_fontAndIcon>
          <MdPermIdentity /> <span>Edgardo Arriagada</span>
        </_fontAndIcon>
        <_fontAndIcon>
          <MdMailOutline /> <span>edgardo.arriagada.ortiz</span>
        </_fontAndIcon>
        <_fontAndIcon>
          <span style={{ marginLeft: "0" }}>@</span> <span>gmail.com</span>
        </_fontAndIcon>
      </_ContactInfo>
      <_Summary>
        <p>
          <strong>Summary</strong>
        </p>
        <p>
          I'm Software Engineer with an IT Engineer Bachelor with 1+ year of
          experience.
        </p>
      </_Summary>
      <_Occupation>
        Software <br /> Engineer
      </_Occupation>
      <_AboutMe href="#about">
        <h2>About me </h2> <_DownArrowIcon />
      </_AboutMe>
    </_Header>
  )
}

export default LandingPage
