import React from "react"

import "normalize.css"
import "./index.scss"
import Layout from "../components/layout"
import About from "../page-components/about"
import Portfolio from "../page-components/portfolio"
import Contact from "../page-components/contact/contact"
import styled, { css } from "styled-components"
import { Themed } from "../styles/theme"

interface SectionProps {
  last?: boolean
}

const _Section = styled.section<SectionProps>`
  text-align: justify;
  width: 100%;
  margin: 0;
  color: ${({ theme }: Themed) => theme.skin.contrastText};
  background-color: ${({ theme }: Themed) => theme.skin.main};
  ${({ last }: SectionProps) =>
    last &&
    css`
      padding-bottom: 4rem;
    `}
`

const _SectionContent = styled.div`
  width: 90%;
  margin: 0 auto;
  @media (min-width: 760px) {
    width: 83%;
  }
  h1 {
    margin: 0;
    color: ${({ theme }: Themed) => theme.skin.dark};
    padding: 3rem 0;
  }
`

const index = () => {
  return (
    <Layout>
      <React.Fragment>
        <_Section>
          <_SectionContent id="about">
            <About />
          </_SectionContent>
        </_Section>
        <_Section>
          <_SectionContent id="portfolio">
            <Portfolio />
          </_SectionContent>
        </_Section>
        <_Section last>
          <_SectionContent id="contact">
            <Contact />
          </_SectionContent>
        </_Section>
      </React.Fragment>
    </Layout>
  )
}

export default index
